<template>
  <Layout>
    <div class="receive-code">
      <div class="receive-form">
        <h1>输入短信验证码</h1>
        <p>
          验证码已发送至 {{ phoneNumber }}<br/>
          请在下方输入6位数字验证码
        </p>
        <div class="code">
          <CodeInput
              :loading="false"
              :class="inputName"
              v-on:change="onChange"
              v-on:complete="onComplete"/>
        </div>
        <span class="countDown">{{ time }}s后重新发送</span>
      </div>
    </div>
  </Layout>
</template>

<script>
import CodeInput from 'vue-verification-code-input'

export default {
  name: 'ReceiveCode',
  components: {
    CodeInput
  },
  data() {
    return {
      phoneNumber: '188****888',
      time: 60,
      isError: false,
      inputName: 'input-name',
    }
  },
  computed: {},
  methods: {
    countDown() {
      let timer = setInterval(() => {
        if (this.time === 0) {
          clearInterval(timer)
          this.time = 60
          return
        }
        this.time--
      }, 1000)
    },
    finish() {
      this.isError = false
      this.$router.push('/')
    },
    onChange(code) {
      console.log(code)
    },
    onComplete(code) {
      console.log(code)
    },
  },
  mounted() {
    this.countDown()
  },
}
</script>

